.calendar-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.calendar-button {
    margin-left: 0.3rem;
    margin-right: 0.3rem;
    min-width: 3rem;
    min-height: 1.8rem;
}

.calendar-controlers,
.calendar-title {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    width: 100%;
}

.calendar-title h2 {
    font-family: 'Raleway', sans-serif;
    font-size: 15px;
    font-weight: bold;
    text-align: left;
    color: #5152C3;
    text-transform: uppercase;
}

.calendar-controlers > div {
    margin-left: 0.3rem;
    margin-right: 0.3rem;
}

.calendar-controlers > button {
    border-radius: 1.5rem;
}

.calendar-week-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.calendar-box {
    padding: 0.6rem 0;
    /* margin: 0.6rem; */
    width: 2.6rem;
    height: 2.6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: #eeeeee; */
}
.calendar-box:hover {
    cursor: pointer;
}

.calendar-box div {
    display: flex;
    text-align: center;
    vertical-align: center;
    justify-content: center;
    align-items: center;
}
.calendar-box-disabled {
    color: #777;
    opacity: 0.3;
}

.calendar-holiday {
    color: #ff3232;
}

.calendar-previous-month,
.calendar-next-month {
    color: #bbbbbb;
}

.calendar-bottom-container {
    text-align: center;
}

.calendar-bottom-container button {
    border-radius: 1.5rem;
}

.calendar-today {
    color: #fafafa;
    border-radius: 0.9rem;
    background-color: #5252ff;
    width: 1.8rem;
    height: 1.8rem;
}

.calendar-box-text {
    padding: 0.6rem;
}

.calendar-selectionBox {
    width: 100%;
    height: 2rem;
    /* background-color: #ababab; */
}

.calendar-selectionBox-selected {
    background-color: aquamarine;
}

.calendar-selectionBox-selected-start {
    border-top-left-radius: 1rem;
    border-bottom-left-radius: 1rem;
}

.calendar-selectionBox-selected-end {
    border-top-right-radius: 1rem;
    border-bottom-right-radius: 1rem;
}

.selectedDate-container {
    display: flex;
    flex-direction: row;
    grid-gap: 1rem;
    gap: 1rem;
    margin-top: 1rem;
}
#calendar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#calendar .calendar-button {
    background-color: aquamarine;
    padding: 0.4rem 0.8rem;
}

#calendar .calendar-title > div {
    font-weight: 600;
}

#calendar .calendar-button.previous-year::after {
    content: '<<';
}
#calendar .calendar-button.previous-month::after {
    content: '<';
}
#calendar .calendar-button.next-month::after {
    content: '>';
}
#calendar .calendar-button.next-year::after {
    content: '>>';
}

#calendar .calendar-bottom-container > button {
    background-color: aquamarine;
    padding: 0.4rem 0.8rem;
}

